<template>
  <div id="projects">
    <img class="wip" src="data:image/png;base64,">
    <a class="icons8" href="https://icons8.com">Icon pack by Icons8</a>  
    <p id="body" class="ft_body fade-in">{{ WIP }}</p>
  </div>
</template>

<script>
export default {
  name: "Projects",
  data() {
    return {
      WIP:
        "This page is currently under construction. Please come back at a later date!"
    };
  }
};
</script>

<style>
#projects {
  background-color: #c09f80;
  height: 100vh;
  width: 100vw;
  position: absolute;
  top: 0;
  z-index: -1;
}
/* Work In Progress Page Code */
.wip {
  position: absolute;
  top: 30%;
  z-index: 20;
  left: 50%;
  margin: 0 0 0 -50px;
}
.icons8 {
  position: absolute;
  bottom: 0;
  text-decoration: none;
  color: white;
  font-size: 0.75em;
  right: 0;
}
#body {
  position: absolute;
  top: 50%;
  margin: 0 5%;
  text-align: center;
  width: 90vw;
}
.ft_body {
  font-family: "Roboto";
  font-weight: 400;
}
/* Work In Progress Page Code End */
/* On page load text animation */
.fade-in {
  opacity: 0;
  animation: fadeIn ease-in 1;
  animation-fill-mode: forwards;
  animation-duration: 1s;
}
/* Keyframes */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(5px);
  }
  to {
    opacity: 1;
    transform: translateY(0px);
  }
}
/* Animation End */
</style>



